import { useState } from 'react';

import Utils from '../../utils/utils'
import { Modal } from 'antd';
import './index.scss'


export function GalleryB(props) {
    const img_url = props.img_url;
    const list = props.dataSource && props.dataSource.list;
    const [visible, setVisible] = useState(false);
    const [currentImg, setCurrentImg] = useState();
    const [title, setTitle] = useState();

    const onContextMenu = (e) => {
        e.preventDefault()
        return false
        //alert("版权保护！")
    }
    const onDragStart = (e) => {
        e.preventDefault()
        return false
    }

    const openGallery = (imgSrc, title) => {
        setVisible(true);
        setCurrentImg(imgSrc);
        setTitle(title);
    }

    const imgList = [];

    if (list) {
        for (var i = 0; i < 4; i++) {
            const info = list[i];
            const bagData = [[], [], []]
            if (info && info.project_image && info.project_image.length > 0) {
                info.project_image.forEach((item, index) => {
                    const url = img_url + info.category + "/" + item.img_url.url
                    const suffix = Utils.getFileType(url);
                    const smalledName = Utils.getSmallSizeFile(url)
                    const imgTypes = ['.gif', '.jpeg', '.png', '.jpg']
                    if (imgTypes.indexOf(suffix.toLowerCase()) > -1) {
                        const work =
                            <div className="work-wrap" key={index} onClick={() => openGallery(url, item.caption)}>
                                <img src={smalledName} alt="" onContextMenu={onContextMenu} onDragStart={onDragStart} />
                                <div className="work-caption">
                                    {item.caption}
                                </div>
                            </div>

                        bagData[index % 3].push(work)
                    }
                })
            }

            const final = <div className="home-area-wrap" key={i}>
                <div className="home-title">
                    <span>{info.desc}</span>
                </div>
                <div className='gallery-wrap'>
                    <div className="imgs-wrap">
                        <div>{bagData[0]}</div>
                        <div>{bagData[1]}</div>
                        <div>{bagData[2]}</div>
                    </div>
                </div>
            </div>
            imgList.push(final)
        }
    }

    return (
        <div>
            {imgList}
            <Modal
                centered
                width={'70%'}
                title={title}
                open={visible}
                onCancel={() => {
                    setVisible(false)
                }}
                footer={null}
            >
                <img src={currentImg} alt="" width="100%" onContextMenu={onContextMenu} onDragStart={onDragStart} />
            </Modal>
        </div>
    )
}
