export default {
    getFileType(file) {
        var filename = file;
        var index1 = filename.lastIndexOf(".");
        var index2 = filename.length;
        var type = filename.substring(index1, index2);
        return type;
    },
    getSmallSizeFile(file) {
        var filename = file;
        var index1 = filename.lastIndexOf(".");
        var index2 = filename.length;
        var pureName = filename.substring(0, index1);
        var type = filename.substring(index1, index2);
        return pureName + "_s" + type;
    },
    getData(url, callback) {
        fetch(url, {
            method: "GET",
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json'
            }
        }).then(response => response.json())
            .then(data => {
                console.log(data);
                callback(data)
            })
    }
}